const { platform, statusBarHeight } = uni.getSystemInfoSync();

export default {
	props: {
		title: String,
		description: String,
		border: {
			type: Boolean,
			default: true
		},
		showBack: {
			type: Boolean,
			default: true
		},
		fixed: Boolean
	},

	computed: {
		top() {
			return platform === "android"
				? `${statusBarHeight}px`
				: "env(safe-area-inset-top)";
		}
	},

	methods: {
		back() {
			uni.navigateBack({
				delta: 1
			});
		},

		tapText(e) {
			this.$emit("click", e);
		}
	}
};
